'use client';

import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { zodResolver } from '@hookform/resolvers/zod';
import {
  Document,
  Page,
  pdf,
  PDFViewer,
  StyleSheet,
  Text,
  View,
} from '@react-pdf/renderer';
import { saveAs } from 'file-saver';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { User } from '../types/user';

// Skema validasi lanjutan
const formSchema = z.object({
  name: z
    .string()
    .min(3, 'Nama minimal 3 karakter')
    .max(50, 'Nama maksimal 50 karakter'),
  email: z.string().email('Email tidak valid').min(5, 'Email terlalu pendek'),
  phone: z
    .string()
    .min(10, 'Nomor telepon minimal 10 digit')
    .regex(
      /^\+?[1-9]\d{9,14}$/,
      'Format telepon tidak valid, contoh: +6281234567890'
    ),
  companyName: z
    .string()
    .max(100, 'Nama perusahaan maksimal 100 karakter')
    .optional(),
});

type FormValues = z.infer<typeof formSchema>;

// Styles buat PDF
const styles = StyleSheet.create({
  page: { padding: 40, fontFamily: 'Helvetica' },
  header: { fontSize: 24, marginBottom: 20, textAlign: 'center' },
  text: { fontSize: 14, marginBottom: 10 },
  branding: {
    fontSize: 10,
    position: 'absolute',
    bottom: 20,
    left: 40,
    color: 'gray',
  },
  watermark: {
    fontSize: 40,
    color: 'rgba(0, 0, 0, 0.1)',
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'rotate(-45deg)',
    textAlign: 'center',
  },
});

// Komponen PDF
const UserProfilePDF = ({
  user,
  template = 'simple',
}: {
  user: FormValues;
  template?: 'simple' | 'detailed';
}) => (
  <Document>
    <Page size='A4' style={styles.page}>
      {template === 'simple' ? (
        <View>
          <Text style={styles.header}>Profil Pengguna</Text>
          <Text style={styles.text}>Nama: {user.name}</Text>
          <Text style={styles.text}>Email: {user.email}</Text>
          <Text style={styles.text}>Telepon: {user.phone}</Text>
        </View>
      ) : (
        <View>
          <Text style={styles.header}>Detail Profil Pengguna</Text>
          <View style={{ borderBottom: 1, marginBottom: 10 }} />
          <Text style={styles.text}>Nama Lengkap: {user.name}</Text>
          <Text style={styles.text}>Alamat Email: {user.email}</Text>
          <Text style={styles.text}>Nomor Telepon: {user.phone}</Text>
          <View style={{ borderBottom: 1, marginTop: 10 }} />
        </View>
      )}
      {user.companyName && (
        <>
          <Text style={styles.branding}>Dibuat oleh: {user.companyName}</Text>
          <Text style={styles.watermark}>{user.companyName}</Text>
        </>
      )}
    </Page>
  </Document>
);

export default function EditUserForm({ user }: { user: User }) {
  const [isPreviewOpen, setIsPreviewOpen] = useState(false);
  const [template, setTemplate] = useState<'simple' | 'detailed'>('simple');

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: user.name,
      email: user.email,
      phone: user.phone,
      companyName: user.company?.name || '',
    },
    mode: 'onChange',
  });

  const formData = form.watch();

  const onSubmit = async (data: FormValues) => {
    const pdfBlob = await pdf(
      <UserProfilePDF user={data} template={template} />
    ).toBlob();
    saveAs(pdfBlob, `${data.name}_profile.pdf`);
  };

  return (
    <>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className='space-y-6'>
          <FormField
            control={form.control}
            name='name'
            render={({ field }) => (
              <FormItem>
                <FormLabel>Nama</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='email'
            render={({ field }) => (
              <FormItem>
                <FormLabel>Email</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='phone'
            render={({ field }) => (
              <FormItem>
                <FormLabel>Telepon</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='companyName'
            render={({ field }) => (
              <FormItem>
                <FormLabel>Nama Perusahaan (Opsional)</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <div className='space-y-4'>
            <div>
              <label className='block text-sm font-medium mb-2'>
                Pilih Template PDF
              </label>
              <RadioGroup
                value={template}
                onValueChange={(value) =>
                  setTemplate(value as 'simple' | 'detailed')
                }
                className='flex space-x-4'
              >
                <div className='flex items-center space-x-2'>
                  <RadioGroupItem value='simple' id='simple' />
                  <FormLabel htmlFor='simple' className='font-normal'>
                    Simpel
                  </FormLabel>
                </div>
                <div className='flex items-center space-x-2'>
                  <RadioGroupItem value='detailed' id='detailed' />
                  <FormLabel htmlFor='detailed' className='font-normal'>
                    Detail
                  </FormLabel>
                </div>
              </RadioGroup>
            </div>
            <div className='space-x-2'>
              <Button type='button' onClick={() => setIsPreviewOpen(true)}>
                Pratinjau PDF
              </Button>
              <Button type='submit' disabled={!form.formState.isValid}>
                Unduh PDF
              </Button>
            </div>
          </div>
        </form>
      </Form>

      {isPreviewOpen && (
        <div className='fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4'>
          <div className='bg-white rounded-lg w-full max-w-3xl h-[80vh] flex flex-col'>
            <div className='p-4 border-b'>
              <Button
                variant='destructive'
                onClick={() => setIsPreviewOpen(false)}
              >
                Tutup
              </Button>
            </div>
            <div className='flex-1 p-4'>
              <PDFViewer width='100%' height='100%' className='w-full h-full'>
                <UserProfilePDF user={formData} template={template} />
              </PDFViewer>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

